@font-face {
  font-family: "LineIcons";
  src: url(/hencework/static/media/LineIcons.b488e590.eot);
  src: url(/hencework/static/media/LineIcons.b488e590.eot) format("embedded-opentype"),
    url(/hencework/static/media/LineIcons.6ef10f14.woff2) format("woff2"),
    url(/hencework/static/media/LineIcons.aa2ecc66.woff) format("woff"),
    url(/hencework/static/media/LineIcons.2c2f484d.ttf) format("truetype"),
    url(/hencework/static/media/LineIcons.dd1368bb.svg) format("svg");
  font-weight: normal;
  font-style: normal;
}
/*------------------------
	base class definition
-------------------------*/
.lni {
  display: inline-block;
  font: normal normal normal 1em/1 "LineIcons";
  color: inherit;
  flex-shrink: 0;
  speak: none;
  text-transform: none;
  line-height: 1;
  vertical-align: -0.125em;
  /* Better Font Rendering */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/*------------------------
  change icon size
-------------------------*/
/* relative units */
.lni-sm {
  font-size: 0.8em;
}
.lni-lg {
  font-size: 1.2em;
}
/* absolute units */
.lni-16 {
  font-size: 16px;
}
.lni-32 {
  font-size: 32px;
}

/*------------------------
  spinning icons
-------------------------*/
.lni-is-spinning {
  -webkit-animation: lni-spin 1s infinite linear;
          animation: lni-spin 1s infinite linear;
}
@-webkit-keyframes lni-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lni-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
/*------------------------
  rotated/flipped icons
-------------------------*/
.lni-rotate-90 {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.lni-rotate-180 {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.lni-rotate-270 {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
}
.lni-flip-y {
  -webkit-transform: scaleY(-1);
          transform: scaleY(-1);
}
.lni-flip-x {
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}
